body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  /*background-image: linear-gradient(140deg, #EADEDB 0%, #BC70A4 50%, #6b5cdf 75%);*/
}
.darkMode{
  color: #FFF;
  background-image: linear-gradient(140deg, #000 0%, #0b0445 75%);

}
.lightMode{
  color: #000;
  background-image: linear-gradient(140deg, #EADEDB 0%, #BC70A4 50%, #6b5cdf 75%);
}
.App{
  height: 100vh;
}
.Container{
  width: 95%;
  max-height: 95vh;
  margin: 0px auto;
  max-width: 960px;
  overflow: hidden;
}

.Quiz{
  margin: 150px auto;
  padding: 15px 20px;
  border-radius:10px;
  max-width: 320px;
  padding-bottom: 40px;
  box-shadow: 1px 1px 10px 1px #0005;
}
.Que{
  margin: 30px 0px;
  font-size: 18px;
}
.Ans{
  padding: 10px 5px;
  background-color: #FFF3;
  margin: 7px 0px;
  border-radius: 5px;
  cursor: pointer;
}


Button{
  display: block;
  padding: 7px 14px;
  margin: 10px;
  background-color: #55F;
  color: #000;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  margin-bottom: 35px;
}

.Header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.darkModeToggle{
  font-size: 28px;
  cursor: pointer;
}

.active{
  font-size: 36px;
  font-weight: bold;
}
.green{
  font-size: 24px;
  font-weight: bold;
}
.scoreCard{
  margin-top: 25px;
  font-size: 18px;
}